export default{
	data(){
		return {
			providerList:[]
		}
	},
	created() {
	    uni.getProvider({
	        service: 'oauth',
	        success: (result) => {
	            this.providerList = result.provider.map((value) => {
	                let providerName = '';
					let icon = '';
					let bgColor = '';
	                switch (value) {
	                    case 'weixin':
	                        providerName = '微信登录'
							icon = 'icon-weixin'
							bgColor = 'bg-success'
	                        break;
	                    case 'qq':
	                        providerName = 'QQ登录'
							icon = 'icon-qq'
							bgColor = 'bg-primary'
	                        break;
	                    case 'sinaweibo':
	                        providerName = '新浪微博登录'
							icon = 'icon-weibo'
							bgColor = 'bg-danger'
	                        break;
	                }
	                return {
	                    name: providerName,
	                    id: value,
						icon:icon,
						bgColor:bgColor
	                }
	            });
	
	        },
	        fail: (error) => {
	            console.log('获取登录通道失败', error);
	        }
	    });
	}
}
